@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  @apply bg-black;
  overflow-x: hidden; 
}

h1, h2, h3 {
  @apply text-white font-bold; 
}

.grid {
  @apply grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4;
}

img {
  @apply w-full max-w-full h-auto;
}
